import React, { Component } from 'react';
import './styles.css';
import '../../../bootstrap.min.css';
import DataFetcher from './DataFetcher';

class TableCreator extends Component {

  htmlCode = '';
  responseObject = null;
  promise = null;
  
  mode = null;
  page = 1;

  checkedAddresses = null;

  constructor(props) {
    super(props);
    console.log("Consturctor:")
    console.log(props);

    this.responseObject = new DataFetcher();
    this.checkedAddresses = new Map();
  }

  renderData(promise) {

    this.promise = promise;

    document.getElementById("type").innerHTML = promise.type;
    document.getElementById("name").innerHTML = promise.name;
    document.getElementById("status").innerHTML = promise.status;

    this.renderTableSummary();
  }

  renderTable() {
    switch(this.mode) {
      case "summary":
        this.renderTableSummary();
        break;
      case "alltransactions":
        this.renderTableAllTransactions();
        break;
      case "selectedTransactions":
        this.selectedTransactions();
        break;
      default:
        break;
    }
  }

  renderTableSummary() {

    console.log("Transaction Summary:");

    this.mode = "summary";

    this.checkedAddresses = new Map();

    this.htmlCode = "";

    let promise = this.promise;

    let transactionSummary = promise.getTransactionSummaryMap();
    console.log(transactionSummary);

    this.addToHTMLCode(
      '<table class="table" style="table-layout:fixed; word-wrap:break-word;">'
      + '<thead class="thead-dark">'
      + '<tr>'
    );

    this.addToHTMLCode('<th scope="col">#</th>');
    this.addToHTMLCode('<th scope="col">Sender</th>');
    this.addToHTMLCode('<th scope="col">Receiver</th>');
    this.addToHTMLCode('<th scope="col">TxCount</th>');

    this.addToHTMLCode(
      '</tr>'
      + '</thead>'
      + '<tbody>');

    
    let rowCounter = 1;
    for (let [key, value] of transactionSummary) {
      console.log(rowCounter + ", " + value.senderName + ", " + value.receiverName + ", " + value.txCount);
      //console.log(key, value);
      this.addToHTMLCode(
        '<tr id="' + key + '">'
        + '<th scope="row"><input class="checkBox" type="checkbox" value="' + key + '" /></th>'
        + '<td>' + value.senderName + '</td>'
        + '<td>' + value.receiverName + '</td>'
        + '<td>' + value.txCount + '</td>'
        + '</tr>'
      );
      rowCounter++;
    }

    this.addToHTMLCode(
      '</tbody>'
      + '</table>'
    );

    document.getElementById("table").innerHTML = this.htmlCode;

    this.addListenerToCheckBoxes();
  }

  renderTableAllTransactions() {

    console.log("TransactionMap");

    this.mode = "alltransactions";

    this.checkedAddresses = new Map();

    this.htmlCode = "";
    //console.log(promise.name);

    let promise = this.promise;

    let transactions = promise.transactionMap;    

    this.addToHTMLCode(
      '<table class="table" style="table-layout:fixed; word-wrap:break-word;">'
      + '<thead class="thead-dark">'
      + '<tr>'
    );

    this.addToHTMLCode('<th scope="col">#</th>');
    this.addToHTMLCode('<th scope="col">Sender</th>');
    this.addToHTMLCode('<th scope="col">Receiver</th>');
    this.addToHTMLCode('<th scope="col">TimeStamp</th>');
    this.addToHTMLCode('<th scope="col">Input</th>');

    this.addToHTMLCode(
      '</tr>'
      + '</thead>'
      + '<tbody>');

    let rowCounter = 1;
    for (let [key, value] of transactions) {
      //console.log(key, value);
      this.addToHTMLCode(
        '<tr>'
        + '<th scope="row">' + rowCounter + '</th>'
        + '<td>' + value.senderName + '</td>'
        + '<td>' + value.receiverName + '</td>'
        + '<td>' + value.timeStamp + '</td>'
        + '<td>' + value.input + '</td>'
        + '</tr>'
      );
      rowCounter++;
    }

    this.addToHTMLCode(
      '</tbody>'
      + '</table>'
    );

    //document.getElementById("table_window").style.visibility = 'visible';
    document.getElementById("table").innerHTML = this.htmlCode;
  }

  renderTableSelectedTransactions() {

    console.log("SelectedTransactionMap");

    this.mode = "selectedtransactions";

    this.htmlCode = "";

    let selectedTransactions = this.promise.getTransactionsForAddress(this.checkedAddresses);  

    this.addToHTMLCode(
      '<table class="table" style="table-layout:fixed; word-wrap:break-word;">'
      + '<thead class="thead-dark">'
      + '<tr>'
    );

    this.addToHTMLCode('<th scope="col">#</th>');
    this.addToHTMLCode('<th scope="col">Sender</th>');
    this.addToHTMLCode('<th scope="col">Receiver</th>');
    this.addToHTMLCode('<th scope="col">TimeStamp</th>');
    this.addToHTMLCode('<th scope="col">Input</th>');

    this.addToHTMLCode(
      '</tr>'
      + '</thead>'
      + '<tbody>');

    let rowCounter = 1;
    for (let [key, value] of selectedTransactions) {
      //console.log(key, value);
      this.addToHTMLCode(
        '<tr>'
        + '<th scope="row">' + rowCounter + '</th>'
        + '<td>' + value.senderName + '</td>'
        + '<td>' + value.receiverName + '</td>'
        + '<td>' + value.timeStamp + '</td>'
        + '<td>' + value.input + '</td>'
        + '</tr>'
      );
      rowCounter++;
    }

    this.addToHTMLCode(
      '</tbody>'
      + '</table>'
    );

    document.getElementById("table").innerHTML = this.htmlCode;    
  }

  addToHTMLCode(code) {
    this.htmlCode = this.htmlCode + code;
  }

  addListenerToCheckBoxes() {
    let checkBoxes = document.getElementsByClassName("checkBox");
    for(let i = 0; i < checkBoxes.length; i++) {
      checkBoxes[i].addEventListener("change", (event) => {
        if(event.currentTarget.checked) {
          this.checkedAddresses.set(event.currentTarget.value, event.currentTarget.value);
          document.getElementById(event.currentTarget.value).style.backgroundColor = "lightgray";
        } else {
          this.checkedAddresses.delete(event.currentTarget.value);
          document.getElementById(event.currentTarget.value).style.backgroundColor = "white";
        }
      });
    }
  }

  getTableHeadlines(transactions) {
    let headlines = [];

    Object.keys(transactions[0]).forEach(function (key) {
      headlines.push(key);
    });

    return headlines;
  }

  scrollAnimation() {
    document.getElementById("table_window").scrollIntoView({
      behavior: "smooth",
      block: "start",
      inline: "nearest"
    });
  }

  showInformation(show) {
    if(show === true) {
      let infoElements = document.getElementsByClassName("info");
      for(let i = 0; i < infoElements.length; i++) {
        infoElements[i].style.display="table-row";
      }
    } else {
      let infoElements = document.getElementsByClassName("info");
      for(let i = 0; i < infoElements.length; i++) {
        infoElements[i].style.display="none";
      }
    }
  }
  showLoader(show) {
    if(show === true) {
      document.getElementById("loader_window").style.display="block";
    } else {
      document.getElementById("loader_window").style.display="none";
      document.getElementById("p").style.width="0%";
      document.getElementById("p").innerHTML="0.0%";
      document.getElementById("loader_info").innerHTML = "";
    }
  }
  showTable(show) {
    if(show === true) {
      document.getElementById("table_window").style.display="block";
    } else {
      document.getElementById("table_window").style.display="none";
    }
  }

  //componentWillMount() {
  //}

  componentDidMount() {
    
    this.showInformation(false);
    this.showLoader(true);
    this.showTable(false);
    let promise = this.responseObject.fetchData(this.props.address);
    promise.then((promise) => {
      this.showLoader(false);
      this.showInformation(true);
      this.showTable(true);
      this.renderData(promise);
      this.scrollAnimation();
    });
  }

  componentWillUpdate(nextProps) {
    this.showInformation(false);
    this.showLoader(true);
    this.showTable(false);
    let promise = this.responseObject.fetchData(nextProps.address);
    promise.then((promise) => {
      this.showLoader(false);
      this.showInformation(true);
      this.showTable(true);
      this.renderData(promise);
      this.scrollAnimation();
    });
  }

  componentDidUpdate() {

  }

  render() {
    return (
      <div class="TableCreator">
        <div id="links">
          <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => this.renderTableSummary()}>Summary</button>
          <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => this.renderTableAllTransactions()}>All transactions</button>
          <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => this.renderTableSelectedTransactions()}>Selected transactions</button>
        </div>
        <table class="page_control">
          <tbody>
          <tr>
            <td>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>First</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>&lt;</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" id="pageBtn" onClick={() => null}>Page 1</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>&gt;</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>Last</button>
            </td>
          </tr>
          </tbody>
        </table>
        <div id="table"></div>
        <table class="page_control">
          <tbody>
          <tr>
            <td>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>First</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>&lt;</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>Page 1</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>&gt;</button>
              <button class="btn btn-outline-secondary" type="button" id="button-addon2" onClick={() => null}>Last</button>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default TableCreator;